import React, {PureComponent} from 'react';
import {Button, withStyles} from '@material-ui/core';
import {TextFieldFormsy} from '@fuse';
import DatepickerFormsy from '@fuse/components/formsy/DatepickerFormsy';
import Formsy from 'formsy-react';

class EventDetailsForm extends PureComponent {
    state = {
        canSubmit: false
    };

    disableButton = () => {
        this.setState({canSubmit: false});
    };

    enableButton = () => {
        this.setState({canSubmit: true});
    };

    onSubmit = (model) => {
        console.info('submit', model);
    };

    render() {
        const {canSubmit} = this.state;
        const {classes} = this.props;
        const itemClass = "my-24 w-full sm:w-1/3 pr-32";

        return (
            <div>
                <Formsy
                    onValidSubmit={this.onSubmit}
                    onValid={this.enableButton}
                    onInvalid={this.disableButton}
                    ref={(form) => this.form = form}
                    className="flex flex-wrap w-full sm:flex-row pb-32"
                >

                    <TextFieldFormsy
                        label="Name"
                        name="eventName"
                        className={itemClass}
                        type="text"
                        validations={{
                            isWords: true,
                            maxLength: 20
                        }}
                        validationErrors={{
                            isWords: 'Only letters and spaces',
                            maxLength: 'Maximum 20 characters'
                        }}
                        required
                    />

                    <TextFieldFormsy
                        label="Location"
                        name="location"
                        className={itemClass}
                        type="text"
                        validations={{
                            isWords: true,
                            maxLength: 20
                        }}
                        validationErrors={{
                            isWords: 'Only letters and spaces',
                            maxLength: 'Maximum 20 characters'
                        }}
                        required
                    />

                    <DatepickerFormsy
                        label="Date *"
                        name="eventDate"
                        wrapperClassName={itemClass}
                        isClearable
                    />

                    <TextFieldFormsy
                        label="Duration (h)"
                        name="duration"
                        className={itemClass}
                        type="text"
                        validations={{
                            isNumeric: true,
                            isPositive: true
                        }}
                        validationError="Value must be multiple of 0.5"
                        required
                    />

                    <TextFieldFormsy
                        label="Price per hour"
                        name="pricePerHour"
                        className={itemClass}
                        type="text"
                        validations={{
                            isInt: true,
                            isPositive: true
                        }}
                        validationError="Positive number only"
                        required
                    />

                    <TextFieldFormsy
                        label="Price per person"
                        name="pricePerPerson"
                        className={itemClass}
                        type="text"
                        validations={{
                            isInt: true,
                            isPositive: true
                        }}
                        validationError="Positive number only"
                        required
                    />

                    {/*<Button*/}
                        {/*type="submit"*/}
                        {/*variant="contained"*/}
                        {/*color="primary"*/}
                        {/*className="mx-auto mt-16"*/}
                        {/*aria-label="LOG IN"*/}
                        {/*disabled={!canSubmit}*/}
                    {/*>*/}
                        {/*Can submit*/}
                    {/*</Button>*/}
                </Formsy>
            </div>
        );
    }
}

const styles = theme => ({});

export default withStyles(styles, {withTheme: true})(EventDetailsForm);

