import React, {PureComponent} from 'react';
import ReactTable from "react-table";
import _remove from 'lodash/remove'
import matchSorter from 'match-sorter'

import data from './participantsData';

class EventParticipants extends PureComponent {
    state = {
        going: [],
        all: data
    };

    handleGoingTrClick = id => {
        const {going, all} = this.state;

        const user = going.find(user => user.id === id);
        _remove(going, user => user.id === id);

        this.setState({going: [...going], all: [...all, user]});
    };

    handleAllTrClick = id => {
        const {going, all} = this.state;

        const user = all.find(user => user.id === id);
        _remove(all, user => user.id === id);

        this.setState({going: [...going, user], all: [...all]});
    };

    render() {
        const {going, all} = this.state;
        return (
            <div className="flex justify-between">
                <ReactTable
                    data={going}
                    columns={columnsGoing}
                    defaultPageSize={14}
                    className="-highlight flex-1 mr-28"
                    showPagination={false}
                    getTrProps={(state, rowInfo) => (!rowInfo ? {} :
                            {
                                onClick: this.handleGoingTrClick.bind(this, rowInfo.original.id)
                            }
                    )}
                    resizable={false}
                />

                <ReactTable
                    className="-striped -highlight flex-1 ml-28"
                    showPagination={false}
                    defaultPageSize={14}
                    data={all}
                    columns={columnsAll}
                    getTrProps={(state, rowInfo) => (!rowInfo ? {} :
                            {
                                onClick: this.handleAllTrClick.bind(this, rowInfo.original.id)
                            }
                    )}
                />
            </div>
        );
    }
}

const columnsGoing = [{
    Header: "",
    id: 'row-index',
    maxWidth: 30,
    filterable: false,
    sortable: false,
    resizable: false,
    Cell: (row) => {
        return <div>{row.index + 1}.</div>;
    }
}, {
    Header: "Alias",
    accessor: "alias"
}];

const columnsAll = [{
    Header: "Alias",
    accessor: "alias"
}, {
    Header: 'Name',
    id: 'name',
    accessor: d => d.name
}, {
    Header: 'Number of events',
    accessor: 'participations'
}];

export default EventParticipants;
